<template>
	<div id="svip-purchase">
		<div class="content">
			<header class="header">
				<div class="user">
					<img :src="member.avatar" alt="" class="avatar">
					<div>
						<div class="info">
							<span>{{member.nickname}}</span>
							<span>（{{member.mobile}}）</span>
						</div>
						<div class="svip">
							<span v-if="1">您还不是超级会员</span>
							<span v-else>您还不是超级会员</span>
						</div>
					</div>
				</div>
			</header>
			<div class="wrapper-item">
				<header class="header-title">
					<h3 class="title">
						选择优惠套餐
						<span class="sub-title">
							<span>会员测试期，购买</span>
							<span class="offer">5折起</span>
						</span>
					</h3>
				</header>
				<section class="item-list">
					<div class="container" :class="{active: select.index == index}" v-for="(item, index) in meals" :key="index">
						<div class="div-all">
							<header class="header-body">
								<h4>{{item.title}}</h4>
								<div class="price">
									<div class="nums">
										<span class="yuan">
											<span>￥</span>{{item.price}}
										</span>
										<del>{{item.oldprice}}</del>
									</div>
								</div>
							</header>
							<span class="tag" v-if="item.description">{{item.description}}</span>
							<small class="tips" v-if="0"></small>
						</div>	
					</div>
				</section>
				<van-cell-group>
					<van-cell title="会员抵扣红包" is-link value="无抵扣红包" />
				</van-cell-group>
			</div>
			<van-cell-group>
				<van-cell title="免费送最高7个月会员" is-link label="办饿了么联名卡送会员，更有百元红包"/>
				<van-cell title="使用兑换码兑换会员" is-link/>
			</van-cell-group>
			<section class="description">
				<router-link to="">自动续费服务条款</router-link>
				<span>|</span>
				<router-link to="">会员特权说明</router-link>
			</section>
			<div class="bottomBar">
				<div class="allprice">
					<div class="total">
						总价
						<span>￥<span>{{select.meal.price}}</span></span>
						<del>{{select.meal.oldprice}}</del>
					</div>
				</div>
				<button>立即开通</button>
			</div>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import {getData} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				showPreLoading: true,
				meals: [],
				member: {},
				select: {
					index: 0,
					meal: {}
				}
			}
		},
		methods: {
			onLoad(id) {
				var that = this;
				getData({
					vue: that,
					url: 'svip/index/meal',
					data: {},
					success: function(res) {
						that.meals = res.meals;
						that.member = res.member;
						that.select.meal = that.meals[0];
					}
				})
			},
			onSelectMeal(index) {
				this.select.meal = this.meals[index];
			}
		},
		mounted(){
			this.onLoad();
		},
	}
</script>

<style>
	#svip-purchase .content{
		position: fixed;
		top: 0;
		right: 0;
		left: 0;
		bottom: 50px;
		width: 100%;
		background: #f5f5f5;
		overflow-x: hidden;
		overflow-y: scroll;
	}
	#svip-purchase .content::-webkit-scrollbar {
		display: none;
	}
	#svip-purchase .content .header{
		background: #282728;
	}
	#svip-purchase .content .header .user{
		display: -webkit-flex;
		display: flex;
		-webkit-align-items: center;
		align-items: center;
		padding: 0 4% 2.934%;
	}
	#svip-purchase .content .header .user .avatar{
		display: block;
		overflow: hidden;
		margin-right: 3.2%;
		margin-top: 7px;
		width: 48px;
		height: 48px;
		border-radius: 50%;
	}
	#svip-purchase .content .header .user .info{
		display: -webkit-flex;
		display: flex;
		-webkit-align-items: baseline;
		align-items: baseline;
		color: #fff;
		line-height: 24px;
		margin-top: 5px;
	}
	#svip-purchase .content .header .user .info span:first-child{
		font-weight: 700;
		font-size: 17px;
	}
	#svip-purchase .content .header .user .info span:last-child{
		font-size: 12px;
	}
	#svip-purchase .content .header .user .svip{
		margin-top: 2.5px;
		color: rgb(255,255,255,0.7);
		line-height: 16px;
		font-size: 12px;
	}
	#svip-purchase .content .wrapper-item{
		background: #fff;
		margin-bottom: 15px;
	}
	#svip-purchase .content .wrapper-item .header-title{
		display: -webkit-flex;
		display: flex;
		padding: 24px 15px 14px;
	}
	#svip-purchase .content .wrapper-item .header-title .title{
		color: #333;
		display: block;
		font-size: 15px;
		font-weight: 700;
		margin-bottom: 5px;
		line-height: 18px;
	}
	#svip-purchase .content .wrapper-item .header-title .title .sub-title{
		color: #999;
		font-size: 12px;
	}
	#svip-purchase .content .wrapper-item .header-title .title .sub-title .offer{
		color: rgb(255,83,57);
		font-size: 12px;
		font-weight: 700;
	}
	#svip-purchase .content .wrapper-item .item-list{
		display: -webkit-flex;
		display: flex;
		-webkit-justify-content: space-between;
		justify-content: space-between;
		padding: 0 15px;
		margin-bottom: 17px;
	}
	#svip-purchase .content .wrapper-item .item-list .container{
		position: relative;
		display: -webkit-flex;
		display: flex;
		-webkit-align-items: center;
		align-items: center;
		-webkit-justify-content: space-between;
		justify-content: space-between;
		height: 150px;
		-webkit-flex: 1;
		flex: 1;
		box-shadow: 0 0 0 1px #eee, 0 1px 1.5px 0 rgba(0,0,0,.04);
		border-radius: 4px;
		background: #fff;
		text-align: center;
	}
	#svip-purchase .content .wrapper-item .item-list .container.active{
		box-shadow: 0 0 0 0.4vw #dfc78a, 0 1px 0.4vw 0 rgba(0,0,0,.04);
		background-color: #fffcee;
	}
	#svip-purchase .content .wrapper-item .item-list .container .div-all{
		width: 100%;
		height: 100%;
	}
	#svip-purchase .content .wrapper-item .item-list .container:nth-child(2){
		margin: 0 10px;
	}
	#svip-purchase .content .wrapper-item .item-list .container .header-body{
		display: -webkit-flex;
		display: flex;
		-webkit-align-items: center;
		align-items: center;
		-webkit-flex-direction: column;
		flex-direction: column;
		-webkit-justify-content: center;
		justify-content: center;
		height: 100%;
	}
	#svip-purchase .content .wrapper-item .item-list .container .header-body h4{
		color: #666;
		font-weight: 700;
		font-size: 16px;
		text-align: center;
		margin-bottom: 4px;
	}
	#svip-purchase .content .wrapper-item .item-list .container.active .header-body h4{
		color: #b99c5b;
	}
	#svip-purchase .content .wrapper-item .item-list .container .header-body .price{
		display: -webkit-flex;
		display: flex;
		-webkit-flex-direction: column;
		flex-direction: column;
	}
	#svip-purchase .content .wrapper-item .item-list .container .header-body .price .nums{
		display: -webkit-flex;
		display: flex;
		-webkit-align-items: center;
		align-items: center;
		-webkit-flex-direction: column;
		flex-direction: column;
	}
	#svip-purchase .content .wrapper-item .item-list .container .header-body .price .nums .yuan{
		font-size: 36px;
		font-weight: 500;
		color: #ff5339;
	}
	#svip-purchase .content .wrapper-item .item-list .container.active .header-body .price .nums .yuan{
		color: #8b5d22;
	}
	#svip-purchase .content .wrapper-item .item-list .container .header-body .price .nums .yuan span{
		font-size: 20px;
		color: #ff5339;
		margin-right: -2px;
	}
	#svip-purchase .content .wrapper-item .item-list .container.active .header-body .price .nums .yuan span{
		color: #8b5d22;
	}
	#svip-purchase .content .wrapper-item .item-list .container .header-body .price .nums del{
		color: #bbb;
		font-size: 14px;
	}
	#svip-purchase .content .wrapper-item .item-list .container.active .header-body .price .nums del{
		color: #b99c5b;
	}
	#svip-purchase .content .wrapper-item .item-list .container .tag{
		position: absolute;
		left: -3px;
		top: -5px;
		font-size: 12px;
		font-weight: 700;
		color: #fff;
		padding: 4px 8px;
		background-color: #ff5339;
		border-radius: 0 6px;
		box-shadow: 0 1px 2px 0 rgba(216,54,30,.2);
	}
	#svip-purchase .content .wrapper-item .item-list .container .tips{
		position: absolute;
		bottom: -.53333vw;
		left: -.4vw;
		width: calc(100% + .8vw);
		padding: 1.6vw 2.4vw;
		text-align: center;
		color: #ff5339;
		font-size: 2.66667vw;
		line-height: 4.4vw;
		background: #fff4e8;
		border-radius: 0 0 1.06667vw 1.06667vw;
		white-space: nowrap;
		text-overflow: ellipsis;
		overflow: hidden;
	}
	#svip-purchase .content .wrapper-item .van-cell__title span{
		color: #333;
		font-size: 15px;
		font-weight: 700;
	}
	#svip-purchase .content .wrapper-item .van-cell__value span{
		color: #9d9d9d;
		font-size: 12px;
		font-weight: 900;
	}
	#svip-purchase .content .van-cell__title span{
		color: #333;
		font-size: 15px;
		font-weight: 700;
	}
	#svip-purchase .content .van-cell__title .van-cell__label{
		font-size: 12px;
		color: #666;
		font-weight: 400;
	}
	#svip-purchase .content .description{
		display: -webkit-flex;
		display: flex;
		-webkit-align-items: center;
		align-items: center;
		-webkit-justify-content: center;
		justify-content: center;
		margin-top: 15px;
		height: 4.26667vw;
	}
	#svip-purchase .content .description a{
		color: #666;
		font-size: 12px;
	}
	#svip-purchase .content .description span{
		margin: 0 13px;
		color: #999;
		font-size: 12px;
	}
	#svip-purchase .content .bottomBar{
		display: -webkit-flex;
		display: flex;
		-webkit-align-items: center;
		align-items: center;
		position: fixed;
		bottom: 0;
		left:  0;
		width: 100%;
		height: 50px;
		background-color: #fff;
		box-shadow: 0 -1px 3px 0 rgba(0,0,0,.1);
	}
	#svip-purchase .content .bottomBar .allprice{
		-webkit-flex: 1;
		flex: 1;
		display: -webkit-flex;
		display: flex;
		-webkit-align-items: center;
		align-items: center;
	}
	#svip-purchase .content .bottomBar .allprice .total{
		display: -webkit-flex;
		display: flex;
		-webkit-align-items: center;
		align-items: center;
		margin-left: 15px;
		font-size: 17px;
		font-weight: 500;
		padding-right: 12px;
	}
	#svip-purchase .content .bottomBar .allprice .total span{
		color: #ff5339;
		margin-left: 4px;
	}
	#svip-purchase .content .bottomBar .allprice .total span span{
		color: #ff5339;
		margin-left: -3px;
	}
	#svip-purchase .content .bottomBar .allprice .total del{
		font-size: 12px;
		color: #999;
		margin-left: 4px;
	}
	#svip-purchase .content .bottomBar button{
		outline: none;
		border: none;
		width: 130px;
		height: 100%;
		background-image: linear-gradient(90deg,#eadb9d,#c9b86a);
		font-size: 17px;
		font-weight: 600;
		color: #5c4415;
	}
</style>


